.button {
    background-color: #508986;
    height: 50px;
    max-width: 400px;
    color: #fff;
    font-size: 16px;
    line-height: 20px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    @media screen and (max-width: 479px) {
        color: #508986;
        background-color: #fff;
    }
}
